$(document).ready(function() {

    // Tabs
    $(".rations__sorting-btn").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".rations__sorting-btn").removeClass("rations__sorting-btn--active")
        $(this).addClass("rations__sorting-btn--active")

        $(this).parents(".rations__inner").find(".rations__cards").removeClass("rations__cards--active")
        $(this).parents(".rations__inner").find(`.rations__cards[data-content-path="${path}"]`).addClass("rations__cards--active")
    })

    // Accordion
    $(".show").click((function(){
        $(this).toggleClass("show--active"),
        $(this).siblings(".hidden").slideToggle()}));

    if (window.matchMedia("(max-width: 992px)").matches) {
        $(".rations-filter__title").click(function () {
            $(this).siblings('.rations-filter__wrap').addClass('rations-filter__wrap--active')
        })

        $(".rations-filter__close2").click(function () {
            $('.rations-filter__wrap').removeClass('rations-filter__wrap--active')
        })
    }

    // Favorite
    $('.ration-card__favorite').click(function () {
        $(this).toggleClass('ration-card__favorite--active')
    })

    // Filter
    $('.rations-filter__mob-btn2').click(function () {
        $('.rations-filter').addClass('rations-filter--active')
        $('body').addClass('fixed-body')
    })

    $('.rations-filter__close').click(function () {
        $('.rations-filter').removeClass('rations-filter--active')
        $('body').removeClass('fixed-body')
    })

    //
    $('.rations-filter__mob-btn').click(function () {
        $('.filter-sorting').addClass('filter-sorting--active')
        $('body').addClass('fixed-body')
    })

    $('.filter-sorting__close').click(function () {
        $('.filter-sorting').removeClass('filter-sorting--active')
        $('body').removeClass('fixed-body')
    })

    //
    $('.rations-filter__clear').click(function () {
        $('.rations-filter__btns').remove()
    })

    $('.rations-filter__btn-mob i').click(function () {
        $(this).parent('.rations-filter__btn-mob').remove()
    })


    // Range
    var handlesSlider = document.getElementById('slider-handles');

    decimals = 0;
    suffix = '₽';

    numberFormat = {
        to: function (value) {
            return value.toFixed(decimals) + ' ' + suffix;
        },
        from: function (value) {
            return Number(value.replace(' ' + suffix));
        }
    };

    noUiSlider.create(handlesSlider, {
        start: [0, 2000],
        range: {
            'min': 0,
            'max': 2000,
        },
        format: numberFormat,
        tooltips: true,
        step: 1,
        connect: true,
    });

    // Range2
    var handlesSlider2 = document.getElementById('slider-handles2');

    decimals = 0;
    suffix = '₽';

    numberFormat = {
        to: function (value) {
            return value.toFixed(decimals) + ' ' + suffix;
        },
        from: function (value) {
            return Number(value.replace(' ' + suffix));
        }
    };

    noUiSlider.create(handlesSlider2, {
        start: [0, 100000],
        range: {
            'min': 0,
            'max': 100000,
        },
        format: numberFormat,
        tooltips: true,
        step: 1,
        connect: true,
    });

    // Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
        var currentele = $(this).find('.select__item-title').html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list li')
    let selectWrap = $('.select__wrap')

    $(document).mouseover(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })


})